<template>
  <div class="list">
  	<div class="list_date" v-for="(item,index) in pageList" :key="index">
  		<div class="title">{{item.title}}</div>
  		<div class="content">
  			<div class="txt">{{item.content}}</div>
  			<div class="detail_btn" @click="look_detail(index)">查看详情>></div>
  		</div>
  	</div>
  </div>
</template>

<script>
// @ is an alias to /src
import store from '@/store'
export default {
  name: 'list',
  store,
  computed:{
  	pageList(){
  		return store.state.lists;
  	},
  },
  methods:{
  	look_detail(index){
			store.commit('look_detail',index);
			this.$router.push('/detail');
  	}
  }
};
</script>

